// app/actions.ts
"use server";

import { PrismaClient } from '@prisma/client';
import { revalidatePath } from 'next/cache';

// Evita instanciar múltiples clientes en desarrollo
const globalForPrisma = global as unknown as { prisma: PrismaClient };
const prisma = globalForPrisma.prisma || new PrismaClient();
if (process.env.NODE_ENV !== 'production') globalForPrisma.prisma = prisma;

// --- LEER TODOS LOS DATOS ---
export async function getFinanceData() {
  const accounts = await prisma.account.findMany();
  const categories = await prisma.category.findMany();
  const transactions = await prisma.transaction.findMany({
    orderBy: { date: 'desc' }
  });
  const fixedRules = await prisma.fixedRule.findMany();
  const simulatorItems = await prisma.simulatorItem.findMany();

  // Convertimos datos complejos para que React los entienda (Fechas y JSON)
  const parsedCategories = categories.map(c => ({
    ...c,
    subCategories: c.subCategories ? JSON.parse(c.subCategories) : []
  }));

  const parsedTransactions = transactions.map(t => ({
    ...t,
    date: t.date.toISOString(), // React prefiere strings para fechas
    accountId: t.accountId,
    categoryId: t.categoryId
  }));

  return {
    accounts,
    categories: parsedCategories,
    transactions: parsedTransactions,
    fixedRules,
    simulatorItems
  };
}

// --- ACCIONES DE TRANSACCIONES ---
export async function createTransaction(data: any) {
  const transactionDate = new Date(data.date);

  // 1. Crear la transacción original (La salida del dinero)
  await prisma.transaction.create({
    data: {
        type: data.type,
        amount: data.amount,
        budgetedAmount: data.budgetedAmount,
        accountId: data.accountId,
        categoryId: data.categoryId,
        description: data.description,
        date: transactionDate,
        isFixed: data.isFixed || false,
        isSavings: data.isSavings || false
    }
  });

  // 2. Actualizar saldo de la cuenta origen
  const multiplier = data.type === 'income' ? 1 : -1;
  await prisma.account.update({
      where: { id: data.accountId },
      data: { balance: { increment: data.amount * multiplier } }
  });

  // 3. MAGIA DE AHORRO: Si es un Gasto marcado como Ahorro, movemos ese dinero a una cuenta de destino
  if (data.type === 'expense' && data.isSavings) {
      // Buscamos una cuenta que tenga "Ahorro" en el nombre o tipo 'wallet'
      // OJO: Esto es automático. Si quieres elegir la cuenta destino, tendríamos que cambiar el formulario.
      const savingsAccount = await prisma.account.findFirst({
          where: { 
             OR: [
                 { name: { contains: 'Ahorro' } },
                 { type: 'wallet' }
             ],
             NOT: { id: data.accountId } // Que no sea la misma cuenta de origen
          }
      });

      if (savingsAccount) {
          // Creamos el ingreso en la cuenta de ahorro automáticamente
          await prisma.transaction.create({
              data: {
                  type: 'income',
                  amount: data.amount,
                  accountId: savingsAccount.id,
                  categoryId: 'cat_sys_savings', // Usamos la categoría especial
                  description: `Ahorro desde: ${data.description}`,
                  date: transactionDate,
                  isSavings: true
              }
          });

          // Sumamos el saldo a esa cuenta de ahorro
          await prisma.account.update({
              where: { id: savingsAccount.id },
              data: { balance: { increment: data.amount } }
          });
      }
  }

  revalidatePath('/'); 
}

// --- ACCIONES DE CUENTAS ---
export async function createAccount(data: any) {
  await prisma.account.create({ data });
  revalidatePath('/');
}

export async function deleteAccount(id: string) {
  await prisma.account.delete({ where: { id } });
  revalidatePath('/');
}

// --- ACCIONES DE CATEGORIAS ---
export async function createCategory(data: any) {
  await prisma.category.create({
    data: {
        name: data.name,
        type: data.type,
        subCategories: JSON.stringify(data.subCategories || [])
    }
  });
  revalidatePath('/');
}

// --- ACCIONES DE SIMULADOR ---
export async function saveSimulatorItem(data: any) {
    // Si existe ID lo actualiza, si no, crea uno nuevo
    const existing = await prisma.simulatorItem.findUnique({ where: { id: data.id } });
    if (existing) {
        await prisma.simulatorItem.update({ where: { id: data.id }, data: { name: data.name, amount: data.amount } });
    } else {
        await prisma.simulatorItem.create({ data });
    }
    revalidatePath('/simulator');
}

export async function deleteSimulatorItem(id: string) {
    await prisma.simulatorItem.delete({ where: { id } });
    revalidatePath('/simulator');
}

// --- REGLAS FIJAS ---
export async function createFixedRule(data: any) {
    await prisma.fixedRule.create({ data });
    revalidatePath('/');
}

// --- FUNCIÓN PARA POBLAR DATOS INICIALES (SEED) ---
export async function seedDatabase() {
  // 1. Crear Cuentas por defecto
  const acc1 = await prisma.account.create({
    data: { name: 'Davivienda', bank: 'Davivienda', type: 'bank', owner: 'Jhonattan', color: '#ef4444', balance: 0 }
  });
  
  const acc2 = await prisma.account.create({
    data: { name: 'Efectivo', bank: 'Bolsillo', type: 'cash', owner: 'Jhonattan', color: '#22c55e', balance: 0 }
  });

  // 2. Crear Categorías por defecto
  await prisma.category.create({
    data: { name: 'Efectivo', type: 'income', subCategories: JSON.stringify(['Retiro']) }
  });

  
  
  // 2. Categorías Básicas
  const catCount = await prisma.category.count();
  if (catCount === 0) {
      await prisma.category.createMany({
          data: [
            { name: 'Efectivo', type: 'income', subCategories: JSON.stringify(['Retiro'])  }
          ]
      });
  }
  
  // 3. Categoría Especial de Ahorro (Esta era la que fallaba)
  // Usamos upsert: Si existe no hace nada (update vacío), si no existe, la crea.
  await prisma.category.upsert({
    where: { id: 'cat_sys_savings' },
    update: {},
    create: { id: 'cat_sys_savings', name: 'Ahorro Automático', type: 'income', subCategories: '[]' }
  });

  revalidatePath('/');
  return { success: true };
}